body {
  margin: 0;
  padding: 0;
  width: 100%;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  font-size: 200%;
  background-color: #f5f5f5;
}

ul{
  margin: 0;
  padding: 0;
  list-style-type: none;
}

h1{
  font-weight: 300;
}

button{
  border: none;
  background: none;
}

.header {
  text-align: center;
  min-height: 200px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  -webkit-box-shadow: 0 0 10px rgba(0,0,0, 0.1);
          box-shadow: 0 0 10px rgba(0,0,0, 0.1);
  margin: 0 0 2rem 0;
}

.header h1 {
  font-size: 5rem;
  background: #ff00cc;
  background: -webkit-linear-gradient(to top, #333399, #ff00cc);
  background: -webkit-gradient(linear, left bottom, left top, from(#333399), to(#FF00CB));
  background: -webkit-linear-gradient(bottom, #333399, #FF00CB);
  background: -o-linear-gradient(bottom, #333399, #FF00CB);
  background: linear-gradient(to top, #333399, #FF00CB);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-weight: 200;
  color: #414141;
}

.content{
  -ms-flex: 5 1;
      flex: 5 1;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-align: center;
      align-items: center;
  text-align: center;
}

.navbar{
  -ms-flex: 1 1;
      flex: 1 1;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  width: 50%;
  margin: 0 auto 2rem auto;
}

.navbar a {
  text-decoration: none;
  text-transform: uppercase;
  color: #414141;
}

.navbar a:hover{
  color: #ff00cc;
  -webkit-transition: 200ms color;
  -o-transition: 200ms color;
  transition: 200ms color;
}

.button{
  background-color: #f5f5f5;
  padding: 24px 14px;
  margin: 1rem;
  border-radius: 5px;
  -webkit-box-shadow: 0 0 6px rgba(0,0,0, 0.1);
          box-shadow: 0 0 6px rgba(0,0,0, 0.1);
  font-size: 2rem;
}

.input{
  border-radius: 5px;
  padding: 4px;
  border: 1px solid #ff00cc;
  -webkit-transition: all 200ms ease;
  -o-transition: all 200ms ease;
  transition: all 200ms ease;
  font-size: 2rem;
  -webkit-box-shadow: 0 0 6px rgba(0,0,0,0.1);
          box-shadow: 0 0 6px rgba(0,0,0,0.1)
}


input:focus {
    border-radius: 5px;
    outline:none;
    border-width: none;
    -webkit-box-shadow: 2px 2px 0 #ff00cc,
                -2px -2px 0#ff00cc,
                -2px 2px 0 #FF00CB,
                2px -2px 0 #ff00cc;
            box-shadow: 2px 2px 0 #ff00cc,
                -2px -2px 0#ff00cc,
                -2px 2px 0 #FF00CB,
                2px -2px 0 #ff00cc;
}


